<template>

  <div v-infinite-scroll="loadMore"
       infinite-scroll-disabled="loading"
       infinite-scroll-distance="0" class="list">
    <div class="list-cell" v-for="item in listData" key="item.id" @click="goMotif(item.id)">
      <!--<router-link :to="'/motif/'+item.id">-->
      <h4 class="cell-title">
        <span v-if="item.top" class="topLab"></span>
        <span v-if="item.good" class="goodLab"></span>
        {{item.title}}
      </h4>
      <div class="cell-msg">
        <div class="user-icon">
          <img :src="item.author.avatar_url">
        </div>
        <p class="user-name">{{item.author.loginname}}</p>
        <p class="cell-tab"><span>{{item.create_at | time-filter}}</span> #{{item.tab | tab-filter}}#</p>
      </div>
      <div class="list-footer">
        <div class="col-33 text-center"><span class="browse-icon">{{item.visit_count}}</span></div>
        <div class="col-33 text-center col-border"><span class="comments-icon">{{item.reply_count}}</span></div>
        <div class="col-33 text-center cell-time">{{item.last_reply_at | time-filter}}</div>
      </div>
      <!--</router-link>-->
    </div>

  </div>
</template>

<script>
  import storage from '../api/Storage'

  export default {
    props: ['listData','isloading','paramsData'],
    data () {
      return {
        loading:true,
      }
    },
    methods: {
      // 滚动条到达底部
      loadMore() {
        if (this.$route.path==='/home') {
          this.$emit('loadMore');
          this.loading = true;
        }

      },
      goMotif(id) {

        let listState = {
          page:this.paramsData.page,
          tab:this.paramsData.tab,
          limit:this.paramsData.limit,
          scroll:Math.max(document.documentElement.scrollTop, document.body.scrollTop)
        };
        storage.setItem('listState',listState);
        this.$router.push('/motif/'+id);
      }
    },
    mounted () {

    },
    watch: {
      // 当请求完成时改变loading
      isloading: function (val, oldVal) {
        let vm = this;
        vm.loading = val;

      }
    },
    beforeDestroy() {

    }
  }
</script>

<style>
  .list {
    width: 100%;
    background-color: #e8e8e8;
    /*padding: 0 0 10px 0;*/
  }

  .list-cell {
    /*width: 100%;*/
    background-color: #fff;
    margin-top: 10px;
    padding: 0 8px;
    -webkit-box-shadow: 3px;
    -moz-box-shadow: 3px;
    box-shadow: 5px 0 5px 0 #aaa;
  }

  .cell-title {
    margin: 0;
    padding: 5px 0;
    line-height: 22px;
  }

  .goodLab:before,.topLab:before{
    color: #fff;
    font-weight: normal;
    padding: 4px;
    font-size: 12px;
    border-radius: 5px;
    margin: -5px 5px 0 0;
  }

  .topLab:before {
    content: '置顶';
    background-color: #FF3333;
  }

  .goodLab:before {
    content: '精华';
    background-color: #00DDDD;
  }

  .cell-msg {
    overflow: hidden;
  }

  .user-icon {
    width: 30px;
    height: 30px;
    float: left;
  }
  .user-icon>img {
    width: 100%;
    border-radius: 4px;
    display:inline-block;
  }

  .user-name {
    line-height: 30px;
    font-weight: 400;
    float: left;
    margin-left: 5px;
  }

  .cell-tab {
    line-height: 30px;
    float: right;
    color: #FFB3FF;
  }

  .cell-tab >span{
    color: #00AA55;
  }

  .list-footer {
    height: 40px;
    /*background-color: aquamarine;*/
    margin-top: 7px;
    border-top: 1px solid #e8e8e8;
  }

  .list-footer >div {
    line-height: 40px;
    position: relative;
    z-index: 1;
  }

  .col-border:before,.col-border:after {
    content: '';
    display: inline-block;
    background-color: #e8e8e8;
    width: 1px;
    height: 26px;
    position: absolute;
    margin-top: 7px;
  }

  .col-border:before {
    left: 0;
  }

  .col-border:after {
    right: 0;
  }

  .browse-icon,.comments-icon,.cell-time {
    color: #707070;
    font-size: 12px;


  }

  .browse-icon,.comments-icon {
    padding-left: 18px;
  }

  .browse-icon {
    background: url("../assets/images/browse.png") no-repeat left;
    background-size: 15px;
  }

  .comments-icon {
    background: url("../assets/images/comments.png") no-repeat left;
    background-size: 13px;
  }

  .list> .loading {
    margin-top: 5px;
  }
</style>



