<template>
  <div>

    <table width="100%" class="home-nav">
        <tbody>
        <tr>
          <td @click="tabNav('all')" :class="{ active : paramsData.tab == 'all' }">全部</td>
          <td @click="tabNav('good')" :class="{ active : paramsData.tab == 'good'}">精华</td>
          <td @click="tabNav('share')" :class="{active: paramsData.tab == 'share'}">分享</td>
          <td @click="tabNav('ask')" :class="{active: paramsData.tab == 'ask'}">问答</td>
          <td @click="tabNav('job')" :class="{active: paramsData.tab == 'job'}">招聘</td>
        </tr>
        </tbody>
      </table>
    <div class="home-content">
        <list :listData="listData" :isloading="isLoading" :paramsData="paramsData" v-on:loadMore="upList"></list>
        <div v-show="isLoading" class="loading">
          <loading></loading>
        </div>
      </div>

  </div>
</template>

<script>

  import DataUrl from '../api/DataUrl'
  import '../filters/filter'
  import loading from './loading.vue'
  import list from './list.vue'
  import storage from '../api/Storage'


  export default {
    data () {
      return {
        paramsData: {
          page:1, // 第几页
          tab:'all', // 分类
          limit:20, // 一页显示多少条
        },
        listData: [],
        isLoading:true,
        isFirstReq:true,
      }
    },
    methods: {
      // 切换选项卡
      tabNav (tabMsg) {
        let vm = this;
        if (vm.paramsData.tab === tabMsg) return;
        vm.paramsData.tab = tabMsg; // 切换tab
        sessionStorage.removeItem('listState');
        vm.listData = [];
        vm.paramsData.page = 1;
        vm.isLoading = true;
        vm.listAjax();
      },
      // 页面到达底部更新数据
      upList () {
        let vm = this;
        vm.isLoading = true;
        vm.paramsData.page++;
        vm.listAjax();
      },
      listAjax() {
        let vm = this;
        let listState = storage.getItem('listState');
        // 请求数据
        let options = {
          params:{
            page:vm.paramsData.page,
            tab:vm.paramsData.tab,
            limit:vm.paramsData.limit
          }
        };

        if (vm.isFirstReq && listState) {
          vm.paramsData.page = listState.page;
          vm.paramsData.tab = listState.tab;
          options.params.limit = listState.page*listState.limit
        }

        DataUrl.getTopics(options).then((res)=>{
          vm.listData = vm.listData.concat(res.body.data);
          vm.isLoading = false;

          if (vm.isFirstReq && listState) {
            vm.$nextTick(function(){
              document.body.scrollTop = listState.scroll;
              vm.isFirstReq = false
            })
          }


        });
      }
    },
    mounted () {
      this.listAjax();
    },
    components: {
      loading,
      list
    },
    beforeDestroy() {

    },
    updated() {

    }
  }

</script>

<style>
  .home-nav {
    text-align: center;
    border-collapse:collapse;
    color: #888;
    position: fixed;
    top: 44px;
    background-color: #fff;
    z-index: 100;
  }

  .home-nav>tbody>tr>td {
    padding: 10px 0;
    border-bottom: 2px solid #e8e8e8;
  }

  .active {
    color: #009FCC;
    border-bottom: 2px solid #009FCC!important;
  }

  .home-content {
    margin-top: 36px;
  }

  .home-content> .loading {
    padding-top: 15px;
  }

</style>



